import {
    Button,
    Switch,
    InputLabel,
    MenuItem,
    FormControl,
    Select,
    TextField,
    autocompleteClasses,
    Box,
    Container,
    Stack,
    Grid,
    Typography
} from "@mui/material"
import axios from 'axios';
import { useState } from "react"
import { useForm } from "react-hook-form";
import { color } from "@mui/system";
import { SelectChangeEvent } from '@mui/material/Select';
import CustomerNames from "./CustomersNames";

import Notification from '../../components/Notifications';


function NewInv(props) {
    const { insernewdata, setOpenPopup2 } = props
    const { register, handleSubmit, formState: { errors } } = useForm({
        mode: "onBlur"
    });
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
    const [amount, setAmount] = useState('')
    const [profitamount, setProfit] = useState('')
    const [total, setTotal] = useState('')
    const [type, setType] = useState('');
    const handleChangetype = (event: SelectChangeEvent) => {
        setType(event.target.value);
    };
    const registerOptions = {
        Account: "",
        ApplicantName: "",
        Address: "",
        Postcode: "",
        mobile: "",
        teloffice: "",
        natureofbusiness: "",
        facilitytype: "",
        dateofestablishment: "",
        purpose: "",
        cost: "",
        contribution: "",
        amount: "",
        period: "",
        security: "",
        nameguarantor: "",
        naturesecurity: "",
        Addresssecurity: "",
        phone: "",

    }

    const handleCancel = () => {
        return setOpenPopup2(false)
    }

    const handleAmount = (event) => {
        // ðŸ‘‡ Get input value from "event"
        setAmount(event.target.value);
    };

    const handleProfit = (event) => {
        // ðŸ‘‡ Get input value from "event"
        setTotal(event.target.value + amount);

        console.log(total)
    };

    function handleRegistration(values) {
        insernewdata(values)
    }

    function handleError(event) { }
    const label = { inputProps: { 'aria-label': 'Switch demo' } };
    return (
        <Container maxWidth="lg" sx={{ margin: 0 }}>
            <Box sx={{ padding: '10px', borderRadius: '10px' }}>
                <Stack direction="row" spacing={2}>
                    <Box flex={2}>
                        <TextField
                            id="Account"
                            label="Account"
                            variant="outlined"
                            placeholder="Account"
                            name="Account"
                            fullWidth
                            margin="normal"
                            {...register('Account', registerOptions.Account)}
                        />
                    </Box>
                    <Box flex={2} >
                        <TextField
                            id="ApplicantName"
                            label="Applicant Name"
                            variant="outlined"
                            placeholder="Applicant Name"
                            fullWidth
                            name="ApplicantName"
                            margin="normal"
                            {...register('ApplicantName', registerOptions.ApplicantName)}
                        />
                       
                        <Typography sx={{ color: 'red' }}>{errors.purpose?.type === "required" && "Purpose is required"}</Typography>
                    </Box>
                    <Box flex={2}>
                        <TextField
                            id="address"
                            label="Full Address"
                            variant="outlined"
                            placeholder="Full Address"
                            fullWidth
                            name="Address"
                            margin="normal"
                            {...register('Address', registerOptions.Address)}
                        />
                       
                        <Typography sx={{ color: 'red' }}>{errors.amount?.type === "required" && "Amount is required"}</Typography>
                    </Box>
                    
                </Stack>


                <br />

                <Stack direction="row" spacing={2}>
                    <Box flex={2} >
                        <TextField
                            id="mobile"
                            label="Mobile"
                            variant="outlined"
                            placeholder="Mobile"
                            fullWidth
                            name="mobile"
                            margin="normal"
                            {...register('mobile', registerOptions.mobile)}
                        />

                        <Typography sx={{ color: 'red' }}>{errors.tenure?.type === "required" && "Tenure is required"}</Typography>
                    </Box>
                    
                    <Box flex={2} >
                        <TextField
                            id="Postcode"
                            label="Post Code"
                            variant="outlined"
                            placeholder="Post Code"
                            fullWidth
                            margin="normal"
                            name="Postcode"
                            {...register('Postcode', registerOptions.Postcode)}
                        />
                        <Typography sx={{ color: 'red' }}>{errors.profitamount?.type === "required" && "Profit Amount is required"}</Typography>
                    </Box>
                    <Box flex={2}>
                        <TextField
                            id="teloffice"
                            label="Tel Office"
                            variant="outlined"
                            placeholder="Tell Office"
                            fullWidth
                            margin="normal"
                            name="teloffice"
                            {...register('teloffice', registerOptions.teloffice)}
                        />
                        <Typography sx={{ color: 'red' }}>{errors.totalamount?.type === "required" && "Total Amount is required"}</Typography>
                    </Box>
                   
                </Stack>

                <br />

                <Stack direction="row" spacing={2}>
                    <Box flex={2} >
                        <TextField
                            id="natureofbusiness"
                            label="Nature of Business"
                            variant="outlined"
                            placeholder="Nature of Business"
                            fullWidth
                            margin="normal"
                            name="natureofbusiness"
                            {...register('natureofbusiness', registerOptions.natureofbusiness)}
                        />
                        <Typography sx={{ color: 'red' }}>{errors.monthlyinstallment?.type === "required" && "Monthly installment is required"}</Typography>
                    </Box>
                    <Box flex={2}>
                        <TextField
                            id="purpose"
                            label="Purpose of finance"
                            variant="outlined"
                            placeholder="Purpose of finance"
                            fullWidth

                            margin="normal"
                            {...register('purpose', registerOptions.purpose)}
                        />
                        <Typography sx={{ color: 'red' }}>{errors.sourceofpayment?.type === "required" && "Source of Payment is required"}</Typography>
                    </Box>
                    <Box flex={2} >
                        <TextField
                            id="totalcost"
                            label="Total Cost"
                            variant="outlined"
                            placeholder="Total Cost"
                            name="cost"
                            margin="normal"
                            {...register('cost', registerOptions.cost)}
                        />
                        <Typography sx={{ color: 'red' }}>{errors.securitydetails?.type === "required" && "Security detail is required"}</Typography>
                    </Box>
                    
                </Stack>

<br/>

                <Stack direction="row" spacing={2}>
                    <Box flex={2}>

                        <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-label">Type of Company</InputLabel>

                            <Select
                                labelId="demo-simple-select-label"
                                id="facilitytype"
                         
                                value={type}
                                label="Facility Type"
                                onChange={handleChangetype}
                                size="medium"
                                name="facilitytype"
                               
                            >
                                <MenuItem value='Proprietorship'>Proprietorship</MenuItem>
                                <MenuItem value='Partnership'>Partnership</MenuItem>
                                <MenuItem value='Limited'>Limited</MenuItem>
                            </Select>


                        </FormControl>
                        <Typography sx={{ color: 'red' }}>{errors.profitrate?.type === "required" && "Profit rate is required"}</Typography>
                    </Box>
                    <Box flex={2}>
                        <TextField
                            id="contribution"
                            label="Your Contribution"
                            variant="outlined"
                            placeholder="Your Contribution"
                            name="contribution"
                            {...register('facilitytype', registerOptions.contribution)}
                        />
                        <Typography sx={{ color: 'red' }}>{errors.securitydescription?.type === "required" && "Security description is required"}</Typography>
                    </Box>
                    <Box flex={2} >
                        <TextField
                            id="amountrequested"
                            label="Amount Requested"
                            variant="outlined"
                            placeholder="Amount Requested"
                            name="amount"
                            {...register('amount', registerOptions.amount)}
                        />
                        <Typography sx={{ color: 'red' }}>{errors.value?.type === "required" && "Value is required"}</Typography>
                    </Box>

                </Stack>
                <br />
                <Stack direction="row" spacing={1}>


                    <Box flex={2} >
                        <TextField
                            id="period"
                            label="Finacing Period"
                            variant="outlined"
                            placeholder="Financing Period"
                            fullWidth
                            name="period"
                            {...register('period', registerOptions.period)}

                        />
                        <Typography sx={{ color: 'red' }}>{errors.contribution?.type === "required" && "Contribution is required"}</Typography>
                    </Box>
                    <Box flex={2} >
                        <TextField
                            id="typeofsecurity"
                            label="Type of security/ colleteral offered"
                            variant="outlined"
                            placeholder="Type of security/ colleteral offered"
                            fullWidth
                            name="security"
                            {...register('security', registerOptions.security)}


                        />
                        <Typography sx={{ color: 'red' }}>{errors.condition?.type === "required" && "Conditions is required"}</Typography>

                    </Box>
                    <Box flex={2} >
                        <TextField
                            id="nameofguarantor"
                            label="Name of guarantor ( in case of third party )"
                            variant="outlined"
                            placeholder="Name of guarantor ( in case of third party )"
                            fullWidth
                            name="nameguarantor"
                            {...register('nameguarantor', registerOptions.nameguarantor)}
                          
                        />
                        <Typography sx={{ color: 'red' }}>{errors.RM?.type === "required" && "RM is required"}</Typography>

                    </Box>
                </Stack>
                <br />

                <Stack direction="row" spacing={1}>


                    <Box flex={2} >
                        <TextField
                            id="phone"
                            label="Phone"
                            variant="outlined"
                            placeholder="Phone"
                            name="phone"
                            {...register('phone', registerOptions.phone)}
                        />
                        <Typography sx={{ color: 'red' }}>{errors.contribution?.type === "required" && "Contribution is required"}</Typography>
                    </Box>
                    <Box flex={2} >
                        <TextField
                            id="natureofbusiesssecurity"
                            label="Nature of business Security"
                            variant="outlined"
                            placeholder="Nature of business Security"
                            name="naturesecurity"
                            {...register('naturesecurity', registerOptions.naturesecurity)}
                        />
                        <Typography sx={{ color: 'red' }}>{errors.condition?.type === "required" && "Conditions is required"}</Typography>

                    </Box>
                    <Box flex={2} >
                        <TextField
                            id="addresssecurity"
                            label="Address"
                            variant="outlined"
                            placeholder="Address"
                            name="Addresssecurity"
                            {...register('Addresssecurity', registerOptions.Addresssecurity)}
                        />
                        <Typography sx={{ color: 'red' }}>{errors.RM?.type === "required" && "RM is required"}</Typography>

                    </Box>
                </Stack>
                <br />

                <Button variant="contained"
                    onClick={handleSubmit(handleRegistration, handleError)}
                >Submit Application</Button>
                <Button onClick={handleCancel}>Cancel</Button>
            </Box>
        </Container>

    )
}
export default NewInv
