function getStepContent(step) {
  switch (step) {
    case 0:
      return <Basicinfo/>;

 
    case 1:
      return <FinancingDetails/>;
    case 2:
      return <Security/>;
    case 3:
      return <Documents/>;
    default:
      return "unknown step";
  }
}

export default function StepForm() {
  const { themeStretch } = useSettingsContext();
  const classes = useStyles();
  const methods = useForm({
    defaultValues: {
Account:"",
ApplicantName:"",
Address:"",
Postcode :"",
mobile:"",
teloffice:"",
natureofbusiness:"",
type:"",
dateofestablishment:"",
purpose:"",
cost :"",
contribution:"",
amount:"",
period:"",
security:"",
nameguarantor:"",
naturesecurity:"",
Addresssecurity:"",
phone:"",
    },
  });
  const [activeStep, setActiveStep] = useState(0);
  const [skippedSteps, setSkippedSteps] = useState([]);
  const steps = getSteps();

  const isStepOptional = (step) => {
    return step === 1 || step === 2;
  };

  const isStepSkipped = (step) => {
    return skippedSteps.includes(step);
  };

  const handleNext = (data) => {
    console.log(data);
    if (activeStep === steps.length - 1) {
      const Endpoint = 'Investments'
      axios.post(variables.API_URL+Endpoint,{
        Account: data.Account,
        ApplicantName: data.ApplicantName,
        Address: data.Address,
        Postcode: data.Postcode,
        mobile: data.mobile,
        teloffice: data.teloffice,
        natureofbusiness: data.natureofbusiness,
        type: data.type,
        dateofestablishment: data.dateofestablishment,
        purpose: data.purpose,
        cost: data.cost,
        contribution: data.contribution,
        amount: data.amount,
        period: data.period,
        security: data.security,
        nameguarantor: data.nameguarantor,
        naturesecurity:data.naturesecurity,
        Addresssecurity: data.Addresssecurity,
        phone: data.phone,
      })
          .then((response) => {
             console.log('created')
             setActiveStep(activeStep + 1);
              
              
          })
          .catch((e) => {
              console.log("Hello errors")
            
          })
    } else {
      setActiveStep(activeStep + 1);
      setSkippedSteps(
        skippedSteps.filter((skipItem) => skipItem !== activeStep)
      );
    }
  };

  const handleBack = () => {
    setActiveStep(activeStep - 1);
  };

  const handleSkip = () => {
    if (!isStepSkipped(activeStep)) {
      setSkippedSteps([...skippedSteps, activeStep]);
    }
    setActiveStep(activeStep + 1);
  };

  return (
    <div>
      <Stepper alternativeLabel activeStep={activeStep}>
        {steps.map((step, index) => {
          const labelProps = {};
          const stepProps = {};
          if (isStepOptional(index)) {
            labelProps.optional = (
              <Typography
                variant="caption"
                align="center"
                style={{ display: "block" }}
              >
                optional
              </Typography>
            );
          }
          if (isStepSkipped(index)) {
            stepProps.completed = false;
          }
          return (
            <Step {...stepProps} key={index}>
              <StepLabel {...labelProps}>{step}</StepLabel>
            </Step>
          );
        })}
      </Stepper>

      {activeStep === steps.length ? (
        <Typography variant="h3" align="center">
          Thank You
        </Typography>
      ) : (
        <>
          <FormProvider {...methods}>
            <form onSubmit={methods.handleSubmit(handleNext)}>
              {getStepContent(activeStep)}

              <Button
                className={classes.button}
                disabled={activeStep === 0}
                onClick={handleBack}
              >
                back
              </Button>
              {isStepOptional(activeStep) && (
                <Button
                  className={classes.button}
                  variant="contained"
                  color="primary"
                  onClick={handleSkip}
                >
                  skip
                </Button>
              )}
              <Button
                className={classes.button}
                variant="contained"
                color="primary"
                 onClick={handleNext}
                type="submit"
              >
                {activeStep === steps.length - 1 ? "Finish" : "Next"}
              </Button>
            </form>
          </FormProvider>
        </>
      )}
    </div>
  );
}
