import { Helmet } from 'react-helmet-async';
import { filter } from 'lodash';
import { useNavigate } from "react-router-dom"
import { sentenceCase } from 'change-case';
import { useState, useEffect } from 'react';
// @mui
import {
    Card,
    Table,
    Stack,
    Paper,
    Avatar,
    Button,
    Popover,
    Checkbox,
    TableRow,
    MenuItem,
    TableBody,
    TableCell,
    Container,
    Typography,
    IconButton,
    TableContainer,
    TablePagination,
} from '@mui/material';



import axios from 'axios';

import VisibilityIcon from '@mui/icons-material/Visibility';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import LibraryAddIcon from '@mui/icons-material/LibraryAdd';
import EditIcon from '@mui/icons-material/ModeEditOutlineOutlined';
import { variables } from "../../variables";
// components
import Label from '../../components/label';
import Iconify from '../../components/iconify';
import Scrollbar from '../../components/scrollbar';
// sections
import { UserListHead, UserListToolbar } from '../../sections/@dashboard/user';
// mock
import USERLIST from '../../_mock/user';

import Popup from '../../components/Popup';
import CustomizedDialogs from '../../components/Dialog/CustomizedDialogs'
import ExistingFacilities from './ExistingFacilities';
import NewUpload from './NewUpload';
import Notification from '../../components/Notifications';
import Edit from './EditInvestment';
import StepForm from './StepForm';

import { useAuthContext } from '../../auth/useAuthContext';
import Invest from './NewInv';
import NewVote from './NewVote';
// ----------------------------------------------------------------------

const TABLE_HEAD = [
    { id: 'account', label: 'Account', alignRight: false },
    { id: 'applicant', label: 'Applicant Name', alignRight: false },
    { id: 'purpose', label: 'Purpsoe', alignRight: false },
    { id: 'amount', label: 'Amount', alignRight: false },
    { id: 'period', label: 'Period', alignRight: false },
    { id: 'status', label: 'Status', alignRight: false },
    { id: 'action', label: 'Action', alignRight: false },
];

// ----------------------------------------------------------------------

function descendingComparator(a, b, orderBy) {
    if (b[orderBy] < a[orderBy]) {
        return -1;
    }
    if (b[orderBy] > a[orderBy]) {
        return 1;
    }
    return 0;
}

function getComparator(order, orderBy) {
    return order === 'desc'
        ? (a, b) => descendingComparator(a, b, orderBy)
        : (a, b) => -descendingComparator(a, b, orderBy);
}

function applySortFilter(array, comparator, query) {
    const stabilizedThis = array.map((el, index) => [el, index]);
    stabilizedThis.sort((a, b) => {
        const order = comparator(a[0], b[0]);
        if (order !== 0) return order;
        return a[1] - b[1];
    });
    if (query) {
        return filter(array, (_user) => _user.customername.toLowerCase().indexOf(query.toLowerCase()) !== -1);
    }
    return stabilizedThis.map((el) => el[0]);
}

export default function UserPage() {
    const [open, setOpen] = useState(null);
    const { user } = useAuthContext();
    const [page, setPage] = useState(0);

    const [order, setOrder] = useState('asc');

    const [selected, setSelected] = useState([]);

    const [orderBy, setOrderBy] = useState('name');

    const [filterName, setFilterName] = useState('');

    const [recordForEdit, SetRecordForEdit] = useState(null)
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
    const [recordForApp, setRecordForApp] = useState(false)
    const [openPopup, setOpenPopup] = useState(false)
    const [openEdit, setOpenUpdate] = useState(false)
    const [openPopup2, setOpenPopup2] = useState(false)
    const [openPopup3, setOpenPopup3] = useState(false)
    const [openPopup4, setOpenPopup4] = useState(false)
    const [openUpload, setUpload] = useState(false)
    const [uploaddocuments, SetUploadDocuments] = useState(null)

    const [rowsPerPage, setRowsPerPage] = useState(5);
    const [rows, setRows] = useState([]);

    const handleOpenMenu = (event) => {
        setOpen(event.currentTarget);
    };

    const handleCloseMenu = () => {
        setOpen(null);
    };

    const handleRequestSort = (event, property) => {
        const isAsc = orderBy === property && order === 'asc';
        setOrder(isAsc ? 'desc' : 'asc');
        setOrderBy(property);
    };

    const handleSelectAllClick = (event) => {
        if (event.target.checked) {
            const newSelecteds = USERLIST.map((n) => n.name);
            setSelected(newSelecteds);
            return;
        }
        setSelected([]);
    };

    const handleClick = (event, name) => {
        const selectedIndex = selected.indexOf(name);
        let newSelected = [];
        if (selectedIndex === -1) {
            newSelected = newSelected.concat(selected, name);
        } else if (selectedIndex === 0) {
            newSelected = newSelected.concat(selected.slice(1));
        } else if (selectedIndex === selected.length - 1) {
            newSelected = newSelected.concat(selected.slice(0, -1));
        } else if (selectedIndex > 0) {
            newSelected = newSelected.concat(selected.slice(0, selectedIndex), selected.slice(selectedIndex + 1));
        }
        setSelected(newSelected);
    };

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setPage(0);
        setRowsPerPage(parseInt(event.target.value, 10));
    };

    const handleFilterByName = (event) => {
        setPage(0);
        setFilterName(event.target.value);
    };

    const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - rows.length) : 0;

    const filteredUsers = applySortFilter(rows, getComparator(order, orderBy), filterName);

    const isNotFound = !filteredUsers.length && !!filterName;


    const getinvestments = async () => {

        try {
        
            const Endpoint = `Investments`
                const data = await axios.get(variables.API_URL + Endpoint);

                setRows(data.data);
            

        }
        catch (e) {
            console.log(e);
        }
    }




    // Displaying Data

    useEffect(() => {
        getinvestments();
    }, []);


    const openInPopup = row => {

        setRecordForApp(row)
        setOpenPopup3(true)

    }

    const openAddDetails = row => {

        SetRecordForEdit(row)
        setOpenPopup(true)

    }

    const openVote = row => {

        SetRecordForEdit(row)
        setOpenPopup4(true)

    }

    const openUploadForm = row => {
        SetUploadDocuments(row)
        setUpload(true)

    }


    const insernewdata = (values) => {

        const Endpoint = 'Investments'
        axios.post(variables.API_URL + Endpoint, {

            account: values.Account,
            applicantName: values.ApplicantName,
            address: values.Address,
            postcode: values.Postcode,
            mobile: values.mobile,
            teloffice: values.teloffice,
            natureofbusiness: values.natureofbusiness,
            type: values.facilitytype,
           // dateofestablishment: values.dateofestablishment,
            purpose: values.purpose,
            cost: values.cost,
            contribution: values.contribution,
            amount: values.amount,
            period: values.period,
            security: values.security,
            nameguarantor: values.nameguarantor,
            naturesecurity: values.naturesecurity,
            addresssecurity: values.Addresssecurity,
            phone: values.phone,
            status: "Proposed"


        })
            .then((response) => {

                setNotify({
                    isOpen: true,
                    message: 'Application Successfuly Submited',
                    type: 'success'
                }

                )

                setOpenPopup2(false)

            })
            .catch((e) => {
                console.log("Hello errors")
                setNotify({
                    isOpen: true,
                    message: 'Employee not hired',
                    type: 'error'
                }
                )
            })
    }

    const insertfacility = (values) => {

        const Endpoint = 'Facilities'
        axios.post(variables.API_URL + Endpoint, {

            FacilityType: values.FacilityType,
            Amountdisbursed: values.Amountdisbursed,
            outstanding: values.outstanding,
            profitrate: values.profitrate,
            totalprofit: values.totalprofit,
            maturitydate: values.maturitydate,
            monthlyinstallment: values.monthlyinstallment,
            Bank: values.Bank,
            applicationid: values.applicationid,

        })
            .then((response) => {

                setNotify({
                    isOpen: true,
                    message: 'Details Added',
                    type: 'success'
                }

                )

                setOpenPopup(false)

            })
            .catch((e) => {
                console.log("Hello errors")
                setNotify({
                    isOpen: true,
                    message: 'Employee not hired',
                    type: 'error'
                }
                )
            })
    }

    const openInUpdate = row => {
        setRecordForApp(row)
        setOpenUpdate(true)
    }

    const updatedata = (values) => {
        console.log(values)
        const Endpoint = `IndividualApp`
        axios.put(variables.API_URL + Endpoint, {
            id: values.applicationid,
            customerid: values.customerid,
            purpose: values.purpose,
            amount: values.amount,
            tenure: values.tenure,
            contribution: values.contribution,
            profitrate: values.profitrate,
            profitamount: values.profitamount,
            totalamount: values.totalamount,
            monthlyinstallment: values.monthlyinstallment,
            securitydetails: values.securitydetails,
            securitydescription: values.securitydescription,
            sourceofpayment: values.sourceofpayment,
            value: values.value,
            condition: values.condition,
            RM: values.RM,
            status: values.status
        })
            .then((response) => {
                setNotify({
                    isOpen: true,
                    message: 'Updated Successfully',
                    type: 'success'
                }
                )
                setOpenPopup3(false);
            })
            .catch((e) => {
                console.log("Hello")
            })
    }

    const navigate = useNavigate();

    const handleView = (row) => {
        setRecordForApp(row)
        navigate("/dashboard/application/views");
    }

    const insertvote = (values) => {

        const Endpoint = 'Voting/investment'
        axios.post(variables.API_URL + Endpoint, {

            recommendation: values.recommendation,
            role: user.role,
            fullname: user.fullname,
            applicationid: values.id,
            email: user?.userName,
            status: values.status,
        })
            .then((response) => {

                setNotify({
                    isOpen: true,
                    message: 'Recommended Added',
                    type: 'success'
                }

                )

                setOpenPopup4(false)

            })
            .catch((e) => {
                console.log("Hello errors")
                setNotify({
                    isOpen: true,
                    message: 'Employee not hired',
                    type: 'error'
                }
                )
            })
    }



    return (
        <>
            <Helmet>
                <title> User | Minimal UI </title>
            </Helmet>

            <Container>
                <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
                    <Typography variant="h4" gutterBottom>
                       Business Investments
                    </Typography>
                    <Button variant="contained" onClick={() => setOpenPopup2(true)} startIcon={<Iconify icon="eva:plus-fill" />}>
                        New
                    </Button>
                </Stack>

                <Card>
                    <UserListToolbar numSelected={selected.length} filterName={filterName} onFilterName={handleFilterByName} />

                    <Scrollbar>
                        <TableContainer sx={{ minWidth: 800 }}>
                            <Table>
                                <UserListHead
                                    order={order}
                                    orderBy={orderBy}
                                    headLabel={TABLE_HEAD}
                                    rowCount={rows.length}
                                    numSelected={selected.length}
                                    onRequestSort={handleRequestSort}
                                    onSelectAllClick={handleSelectAllClick}
                                />
                                <TableBody>
                                    {filteredUsers.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
                                        //  const { id,customername, mobile, status, purpose,amount,profitamount} = row;
                                        const selectedUser = selected.indexOf(row.customername) !== -1;

                                        return (
                                            <TableRow hover key={row.id} tabIndex={-1} role="checkbox" selected={selectedUser}>
                                                <TableCell padding="checkbox">
                                                    <Checkbox checked={selectedUser} onChange={(event) => handleClick(event, row.customername)} />
                                                </TableCell>

                                                <TableCell component="th" scope="row" padding="none">
                                                    <Stack direction="row" alignItems="center" spacing={2}>
                                                        <Avatar alt={row.applicantName} />
                                                        <Typography variant="subtitle2" noWrap>
                                                            {row.account}
                                                        </Typography>
                                                    </Stack>
                                                </TableCell>

                                                <TableCell align="left">{row.applicantName}</TableCell>
                                                <TableCell align="left">{row.purpose}</TableCell>
                                                <TableCell align="left">{row.amount}</TableCell>
                                                <TableCell align="left">{row.period}</TableCell>
                                                <TableCell align="left">
                                                    <Label color={(row.status === 'Proposed' && 'info') || (row.status === 'Declined' && 'error') || (row.status === 'Approved' && 'success') || (row.status === 'Pending' && 'warning') || (row.status === 'Process' && 'success') || (row.status === 'Executed' && 'primary')} >{sentenceCase(row.status)}</Label>
                                                </TableCell>

                                                <TableCell align="right">
                                                    <Button variant='bg-green-500 mx-2' onClick={() => openInPopup(row)} startIcon={<EditIcon />} />
                                                    <Button variant='bg-cayn-500 mx-2' onClick={() => navigate({
                                                        pathname: `/dashboard/application/viewinvestments/${row.id}`
                                                    })} startIcon={<VisibilityIcon />} color="info" />
                                                    
                                                    {user?.role === "COM" && (
                                                        <Button variant='bg-red-500 mx-2' onClick={() => openVote(row)} startIcon={<LibraryAddIcon />} color="error" />
                                                    )
                                                    }

                                                    {/* <IconButton size="large" color="inherit" onClick={handleOpenMenu}>
                            <Iconify icon={'eva:more-vertical-fill'} ref />
                          </IconButton> */}
                                                </TableCell>
                                            </TableRow>
                                        );
                                    })}
                                    {emptyRows > 0 && (
                                        <TableRow style={{ height: 53 * emptyRows }}>
                                            <TableCell colSpan={6} />
                                        </TableRow>
                                    )}
                                </TableBody>

                                {isNotFound && (
                                    <TableBody>
                                        <TableRow>
                                            <TableCell align="center" colSpan={6} sx={{ py: 3 }}>
                                                <Paper
                                                    sx={{
                                                        textAlign: 'center',
                                                    }}
                                                >
                                                    <Typography variant="h6" paragraph>
                                                        Not found
                                                    </Typography>

                                                    <Typography variant="body2">
                                                        No results found for &nbsp;
                                                        <strong>&quot;{filterName}&quot;</strong>.
                                                        <br /> Try checking for typos or using complete words.
                                                    </Typography>
                                                </Paper>
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                )}
                            </Table>
                        </TableContainer>
                    </Scrollbar>

                    <TablePagination
                        rowsPerPageOptions={[5, 10, 25]}
                        component="div"
                        count={USERLIST.length}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                    />
                </Card>
            </Container>

            <Popover
                open={Boolean(open)}
                anchorEl={open}
                onClose={handleCloseMenu}
                anchorOrigin={{ vertical: 'top', horizontal: 'left' }}
                transformOrigin={{ vertical: 'top', horizontal: 'right' }}
                PaperProps={{
                    sx: {
                        p: 1,
                        width: 140,
                        '& .MuiMenuItem-root': {
                            px: 1,
                            typography: 'body2',
                            borderRadius: 0.75,
                        },
                    },
                }}
            >


                <MenuItem sx={{ color: 'error.main' }}  >
                    <Iconify icon={'eva:edit-fill'} sx={{ mr: 2 }} />
                    Edit
                </MenuItem>
                <MenuItem sx={{ color: 'error.main' }}>
                    <Iconify icon={'eva:trash-2-outline'} sx={{ mr: 2 }} />
                    Delete
                </MenuItem>
            </Popover>

            <Popup
                openPopup={openPopup3}
                setOpenPopup={setOpenPopup3}
                title="Edit Application"
            >
                <Edit
                    recordForApp={recordForApp}
                    insertdata={updatedata}
                    setOpenPopup3={setOpenPopup3}

                />


            </Popup>


            <Popup
                openPopup={openPopup}
                setOpenPopup={setOpenPopup}
                title="Existing Facilities"
            >
                <Edit
                    recordForEdit={recordForEdit}
                    insertfacility={insertfacility}
                    setOpenPopup={setOpenPopup}
                />
            </Popup>

            <Popup
                openPopup={openUpload}
                setUpload={setUpload}
                title="New Uploads"
            >
                <NewUpload
                    uploaddocuments={uploaddocuments}
                    setUpload={setUpload}
                />


            </Popup>

            <Popup
                openPopup={openPopup2}
                setOpenPopup={setOpenPopup2}
                title="New Investment"
            >
                <Invest insernewdata={insernewdata} setOpenPopup2={setOpenPopup2} />
            </Popup>

            <Popup
                openPopup={openPopup4}
                setOpenPopup4={setOpenPopup4}
                title="Application Recommendation"
            >


                <NewVote
                    recordForEdit={recordForEdit}
                    insertvote={insertvote}
                    setOpenPopup4={setOpenPopup4}
                />


            </Popup>

            <Notification
                notify={notify}
                setNotify={setNotify}
            />

        </>
    );
}
